import Vue from "vue"
import Router from "vue-router"

import Landing from "@/components/Landing"
import Reader from "@/components/Reader"

Vue.use(Router)

export default new Router({
  routes: [{
    path: "/",
    name: "Landing",
    component: Landing
  }, {
    path: "/:id",
    name: "Reader",
    component: Reader
  }]
})



